/**
 * Spesifikasi warna secara umum.
 */
$colorCorporate: #0066b2;
$colorCorporateDarker: #004070;
$colorMainBackground: #f7f7f7;
$colorMainForeground: #000000;
$colorContentBackground: #ffffff;
$colorContentBorder: #939393;
$colorFormBackground: #EBEBEB;
$colorFormBorder: #939393;
$colorButtonForeground: #ffffff;
$colorButtonActiveBackground: #ffffff;
$colorButtonActiveForeground: #000000;
$colorFooterForeground: #ffffff;
$colorModalBackground: #0066b2;
$colorModalForeground: #ffffff;
$colorButtonSuccess: #1B9E00;
$colorButtonSuccessHover: #0F5500;
$colorButtonDanger: #9A0403;
$colorButtonDangerHover: #4F0100;

/**
 * Reset padding default dari bootstrap.
 */
.alt-grid {
    [class*="col-"] {
        padding-left:0;
        padding-right:0;
    }

    .row {
        margin-left:0;
        margin-right:0;
    }

    .container-fluid {
        width:100%;
        max-width:none;
        padding:0;
    }
}

/**
 * Spesifikasi CSS Utama.
 */
html, body {
    height: 100%;
    background-color: $colorMainBackground;
    font-family: 'Open Sans', 'Trebuchet Ms', sans-serif;
}

header {
    .top-pad {
        background-color: $colorCorporate;
        transition: all 0.4s ease;
    }

    nav.navbar {
        background-color: $colorContentBackground;
        border: none;
        border-radius: 0;
        border-bottom: $colorCorporateDarker 2px solid;
        transition: all 0.4s ease;

        .navbar-header {
            margin-top: -10px;
        }
    }

    .brand-image {
        transition: all 0.4s ease;
    }

    .app-title {
        font-size: 9.55px;
        font-weight: bold;
        transition: all 0.4s ease;
    }

    .navbar-toggle {
        margin-top: 15px;
        margin-bottom: 5px;
    }

    ul > li {
        text-align: center;
    }
}

.nav > li > a {
    padding-left: 10px;
    padding-right: 10px;
}

footer > .footer-content {
    background-color: $colorCorporate;
    color: $colorFooterForeground;
    border-top: $colorCorporateDarker 2px solid;
    position: fixed;
    bottom: 0;
    width: 100%;
    z-index: 100;
}

.footer-content {
    font-size: 0.9em;
    font-weight: bold;
    line-height: 1.8em;
    text-align: center;
}

.main-container {
    margin: 0 auto;
    padding-bottom: 30px;
}

.main-content {
    background-color: $colorContentBackground;
    border: 2px solid $colorContentBorder;
    margin-bottom: 20px;
    padding: 0 20px 20px 20px;

    hr {
        margin-top: 0;
        margin-bottom: 1.25em;
    }
}

.form-control {
    background-color: $colorFormBackground;
    border: 2px solid $colorFormBorder;
    border-radius: 0;
}

.modal-header, .modal-footer {
    background-color: $colorModalBackground;
    color: $colorModalForeground;
}

.modal-header {
    border-radius: 5px 5px 0 0;
    text-align: center;

    h4 {
        font-weight: bold;
    }
}

.header-greeting {
    background-color: $colorCorporate;
    border-bottom: $colorCorporateDarker 2px solid;
    border-left: $colorCorporateDarker 2px solid;
    border-right: $colorCorporateDarker 2px solid;
    border-radius: 0 0 5px 5px;
    color: #ffffff;
    font-weight: bold;
    margin-top: -20px;
    padding: 2px 25px 2px 25px;
    text-align: right;
}

th {
    font-weight: bold;
    text-align: center;
}

/**
 * Spesifikasi untuk button.
 */
.btn-submit, .btn-confirm, .btn-decline {
    border: 2px solid $colorFormBorder;
    border-radius: 0;
    color: $colorButtonForeground;
    font-weight: bold;
    text-align: center;
    width: 100%;
}

.btn-submit {
    background-color: $colorCorporate;

    &:hover {
        background-color: $colorCorporateDarker;
        color: $colorButtonForeground;
    }
}

.btn-confirm {
    background-color: $colorButtonSuccess;

    &:hover {
        background-color: $colorButtonSuccessHover;
        color: $colorButtonForeground;
    }
}

.btn-decline {
    background-color: $colorButtonDanger;

    &:hover {
        background-color: $colorButtonDangerHover;
        color: $colorButtonForeground;
    }
}

.btn-submit:active, .btn-confirm:active, .btn-decline:active {
    background-color: $colorButtonActiveBackground;
    color: $colorButtonActiveForeground;
}

.btn-compact {
    width: auto;
}

/**
 * Spesifikasi CSS untuk ketika scroll ke halaman bawah (perkecil).
 */
.mini-head {
    .top-pad {
        height: 5px;
    }

    nav.navbar {
        min-height: 30px;
    }

    .brand-image {
        width: 80px;
    }

    .app-title {
        margin-top: -20px;
        margin-left: 85px;
    }

    .navbar-nav>li>a {
        padding-top: 8px;
        padding-bottom: 7px;
    }

    .nav-name {
        display: none;
    }
}

/**
 * Spesifikasi khusus untuk ukuran yang lebih kecil (mobile view).
 */
@media screen and (max-width: 768px) {
    .top-pad {
        height: 5px;
    }

    nav.navbar {
        min-height: 30px;
    }

    .app-title {
        margin-top: -20px;
        margin-left: 85px;
    }

    .brand-image {
        width: 80px;
    }

    .header-greeting {
        font-size: 0.7em;
    }

    .main-container {
        margin-top: 80px;
    }

    .main-content {
         h1 {
             font-size: 1.55em;

             & > a {
                 font-size: 0.45em;
             }
         }
     }
}

/**
 * Spesifikasi untuk ukuran biasa (tablet, desktop dll).
 */
@media screen and (min-width: 769px) {
    .top-pad {
        height: 20px;
    }

    nav.navbar {
        min-height: 80px;
    }

    .brand-image {
        width: 159px;
    }

    .app-title {
        margin-top: -15px;
        margin-left: 60px;
    }

    .main-container {
        margin-top: 120px;
    }
}

/**
 * Spesifikasi untuk action pada tabel.
 */
a.action-green {
    color: #009A3F;
}

a.action-yellow {
    color: #919000;
}

a.action-red {
    color: #b52b27;
}

a[class*="action-"] {
    text-decoration: none;

    &:hover {
        font-weight: bold;
    }
}

.huge {
    font-size: 26px;
    font-weight: bold;
}

/**
 * Panel candidate view.
 */
.panel {
    border-radius: 0;
    border: 0;
}

.panel-heading {
    border-radius: 0;
}

.panel-footer {
    border-radius: 0;
    background-color: #ffffff;
}

.tab-content {
    padding-top: 10px;
}

.nav-item.active {
    font-weight: bold;
}

/**
 * Spesifikasi untuk pewarnaan button dengan badge.
 */
.link-green {
    color: #00A800 !important;

    .badge {
        background-color: #00A800;
    }
}

.link-orange {
    color: #BD6F00 !important;

    .badge {
        background-color: #BD6F00;
    }
}

.link-blue {
    color: #006DAB !important;

    .badge {
        background-color: #006DAB;
    }
}

.link-gray {
    color: #3E3E3E !important;

    .badge {
        background-color: #3E3E3E;
    }
}

.link-dark-green {
    color: #007871 !important;

    .badge {
        background-color: #007871;
    }
}

.link-dark-red {
    color: #A85957 !important;

    .badge {
        background-color: #A85957;
    }
}

.link-light-gray {
    color: #737373 !important;

    .badge {
        background-color: #737373;
    }
}

/**
 * Spesifikasi untuk detail kandidat dalam modal.
 */
.candidate-details, .candidate-project-name {
    font-family: "Open Sans";
}

.overall-information {
    h4 {
        margin-top: 0;
    }
}

.candidate-details {
    .panel {
        border: 1px solid #ddd;
    }

    .panel-default > .panel-heading {
        background-color: #004070;
        color: #fff;
    }
}

/**
 * Spesifikasi untuk form validation.
 */
 textarea {
     resize: none;
 }

 a:hover {
     cursor: pointer;
 }

label.has-error {
    color: $colorButtonDanger;
    font-size: 0.8em;
}

/**
 * Spesifikasi untuk select 2 js.
 */
.select2-selection--single {
    border: 2px solid black;
    border-radius: 0px;
}

.select2-container--default .select2-selection--single {
    background-color: #eee;
    border: 2px solid #aaa;
    border-radius: 0;
}

.select2-container .select2-selection--single {
    height: auto;
}
